.Skin {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 80vh;
  width: 60vw;
  gap: 20px;
  border: 5px solid #ffffff;
  padding: 20px;
  border-radius: 15px;
  background-color: blue;

  &__diagram {
    display: flex;
    background-color: #c5f1b2;
    padding: 20px;
    height: 60vh;
    width: 90%;
    border: 3px solid #fff;
    border-radius: 15px;
  }

  &__input {
    display: flex;
    &__text {
      color: #fff;
      margin-right: 25px;
      text-align: center;
    }
  }

  &__xValues {
    border: 3px solid #fff;
    border-radius: 15px;
    background-color: #c5f1b2;
    text-align: center;
  }

  &__yValues {
    border: 3px solid #fff;
    border-radius: 15px;
    background-color: #c5f1b2;
    text-align: center;
  }

  &__buttons {
    display: flex;
    gap: 20px;
    &__text {
      color: #fff;
    }
  }
}